.worldcaseCon {
    width: 100%;
}
.worldcaseCon .headings {
    margin: 40px 0 10px;
    font-size: 36px;
}
.updated  {
    font-size: 12px;
    font-weight: normal;
    font-family: sans-serif;
}
.worldcaseCon .totalUpdates {
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: center;
    flex-flow: wrap;
    margin: 35px 0;
}
.worldcaseCon .updateBox {
    width: 240px;
    height: 200px;
    text-align: center;
    padding: 10px;
    border-radius: 4px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-flow: column;
    margin: 5px;
}
.worldcaseCon .updateBox p {
    color: #fff;
}
p.tally {
    font-size: 48px;
}
p.updateText {
    font-size: 16px;
}
.worldcaseCon .total {
    background: #ea0000;
}
.worldcaseCon .active {
    background: #fd5505;
}
.worldcaseCon .recovered {
    background: #2bad00;
}
.worldcaseCon .death {
    background: #5d5d5d;
}
.worldcaseCon .critical {
    background: #ff9900;
}
.worldcaseCon .affected {
    background: #f16022;
}
@media(max-width: 767px){
    .worldcaseCon .totalUpdates {
        justify-content: center;
    }
    .worldcaseCon .updateBox {
        width: 180px;
        height: 180px;
    }
    .worldcaseCon .headings {
        text-align: center;
        font-size: 30px;
    }
    p.tally {
        font-size: 30px;
    }
}