header {
    width: 100%;
    padding: 25px 15px;
    transition: all .5s ease;
}
header ul {
    display: flex;
    justify-content: center;
    align-items: center;
    list-style: none;
}
header ul li {
    margin: 0 15px;
}
header ul li a {
    color: #000;
    text-decoration: none;
    font-size: 16px;
    font-weight: bold;
    transition: all .5s ease;
}
header ul li a:hover, header ul li a.active {
    text-decoration: none;
    color: #020202;
    transition: all .5s ease;
    font-weight: bold;
    font-size: 21px;
}
header nav {
    transition: all .5s ease;
}
@media(max-width: 767px){
    header ul {
        display: flex;
        justify-content: left;
        align-items: flex-start;
        list-style: none;
        flex-flow: column;
        position: fixed;
        z-index: 9;
        width: 100%;
        background: #fff;
        height: 100%;
        top: 0;
        padding: 25px;
        transform: translateX(-125%);
        transition: all .5s ease;
    }
    nav.active ul {
        transform: translateX(0%);
        transition: all .5s ease;
    }
    header .hamburger-icon {
        border: 1px solid #bbbbbb;
        display: flex;
        justify-content: center;
        flex-flow: column;
        align-items: center;
        width: 45px;
        padding: 5px;
        border-radius: 5px;
        box-sizing: border-box;
        cursor: pointer;
    }
    header .hamburger-icon span {  
        display: block;
        height: 3px;
        background: #000;
        width: 30px;
        margin: 2px auto;
    }
    nav.active {
        z-index: 10;
        position: fixed;
        width: 75%;
        left: 0px;
        top: 0;
        height: 100%;
        transform: translateX(0%);
        transition: all .5s ease;
    }
    
    nav.active ul li {
        margin: 5px 0px;
    }
}