html, body, * {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'Roboto Condensed', sans-serif;
}
section {
    max-width: 1200px;
    margin: 0 auto;
}
section.main {
    padding: 15px;
}
.selectedCountry {
    display: flex;
    align-items: center;
    font-size: 21px;
    line-height: 18px;
    margin: 0 0 10px;
    padding: 0;
}
.selectedCountry span {
    margin-right: 15px;
}
.selectedCountry span img {
    max-width: 50px;
}
h1, h2 {
    margin: 0 0 25px;
    font-size: 21px;
}
.selectCountry {
    font-size: 14px;
    padding: 4px 10px;
    border-radius: 3px;
}