.eachCountry {
    width: 250px;
    margin: 5px;
    padding: 5px;
    border: 1px solid #bdbdbd;
    border-radius: 3px;
}
.countryInfo {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px 0;
}
.countryInfo img {
    max-width: 30px;
    margin: 0 10px 0;
}
.countryInfo h2{
    font-size: 16px;
    margin: 0;
}
.countryStats p {
    font-size: 14px;
    padding: 2px 5px;
}
.countryStats p:nth-of-type(odd){
    background: #f7f5f5;
}
.updated {
    font-size: 11px;
    font-family: sans-serif;
    padding: 5px;
}