.homecase {
    width: 100%;
}
.homecase .headings {
    margin: 40px 0 5px;
    font-size: 21px;
    line-height: 1em;
}
.update  {
    font-size: 12px;
    font-weight: normal;
    font-family: sans-serif;
}
.homecase .totalUpdates {
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: center;
    flex-flow: wrap;
    margin: 35px 0;
}
.homecase .updateBox {
    width: 240px;
    height: 200px;
    text-align: center;
    padding: 10px;
    border-radius: 4px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-flow: column;
    margin: 5px;
}
.homecase .updateBox p {
    color: #fff;
}
p.tally {
    font-size: 48px;
}
p.updateText {
    font-size: 16px;
}
.homecase .total {
    background: #ea0000;
}
.homecase .active {
    background: #fd5505;
}
.homecase .recovered {
    background: #2bad00;
}
.homecase .death {
    background: #5d5d5d;
}
.homecase .critical {
    background: #ff9900;
}
@media(max-width: 767px){
    .homecase .totalUpdates {
        justify-content: center;
    }
    .homecase .updateBox {
        width: 180px;
        height: 180px;
    }
    .homecase .headings {
        text-align: center;
        font-size: 30px;
    }
    p.tally {
        font-size: 30px;
    }
    .update  {
        text-align: center;
    }
}